.leaderboard-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.leaderboard-title {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
}

.leaderboard-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.leaderboard-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #f8f9fa;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.profile-picture {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 1rem;
  color: #fff;
  text-transform: uppercase;
}

.bg-color-1 { background-color: #FF5733; }
.bg-color-2 { background-color: #C70039; }
.bg-color-3 { background-color: #900C3F; }
.bg-color-4 { background-color: #581845; }
.bg-color-5 { background-color: #1C1C1C; }
.bg-color-6 { background-color: #DAF7A6; }
.bg-color-7 { background-color: #FFC300; }
.bg-color-8 { background-color: #FF5733; }
.bg-color-9 { background-color: #C70039; }
.bg-color-10 { background-color: #900C3F; }

.current-user-box {
  margin-top: 20px;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.current-user-box h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.message-box {
  margin-top: 20px;
  padding: 20px;
  background-color: #e9ecef;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.message-box p {
  margin: 0;
  font-size: 1rem;
}

.forum-link {
  display: inline-block;
  margin-top: 10px;
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.forum-link:hover {
  text-decoration: underline;
}
